import CertItem from "./CertItems";

import "./Certs.css";

const Certs = (props) => {
  return (
    <div>
      <div className="infoText1">
        <div>
          <img src="GoDaddyLogo.jpg" alt="GoDaddy Logo"></img>
        </div>
        <h2>SSL Certificates</h2>
      </div>
      <div className="infoText2">
        <p>Sorted by Certificate Expiry</p>
      </div>
      {props.items.map((cert) => (
        <CertItem
          subjectName={cert.subjectname}
          subStart={new Date(cert.subscriptionstart)}
          subEnd={new Date(cert.subscriptionend)}
          certExpiry={new Date(cert.certificateexpiry)}
        />
      ))}
    </div>
  );
};

export default Certs;
