import Certs from "./components/Certs/Certs";

import { useState, useEffect } from "react";

const App = () => {
  const [SSLCerts, setSSLCerts] = useState([]);

  const readCertsFile = async () => {
    const certData = await fetch("http://localhost:3000/godaddyCerts.json");

    const certDataJSON = await certData.json();

    const GODADDY_CERTS_SORTED_ASC = certDataJSON.sort(
      (objA, objB) =>
        Number(new Date(objA.certificateexpiry)) -
        Number(new Date(objB.certificateexpiry))
    );

    setSSLCerts(GODADDY_CERTS_SORTED_ASC);
  };

  useEffect(() => {
    readCertsFile();
  }, []);

  return (
    <div>
      <Certs items={SSLCerts} />
    </div>
  );
};

export default App;
